    font-weight: 300;
    font-size: 12px;
    line-height: 358%;
    color: #9B9B9B;
    padding: 0 auto;
    text-transform: none;
    outline: none;
}

.Gradebook .content .table{
    color:black;
}

.Gradebook .content .warning-button{
    background: #FFE01C;
    border-radius: 8px;
    text-transform: none;
    font-family: Rubik;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 157%;
    letter-spacing: 1.8px;
    color: #000000;
}

.Gradebook .table .header-row{
    box-shadow:none;
    border:0px;
}

.Gradebook .table .header-row .head-cell{
    font-weight: 300;
}
.Gradebook .table .t-row{
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.25);
    margin-bottom: 1rem;
}
.Gradebook .table .t-row .cell{
    font-weight: 500;
}
.navbar{
    background-color: transparent;
    position: fixed;
    width: 100%;
    border-radius: 0;
    z-index: 1;
}
.navbar-light .navbar-nav .nav-item{
    border-radius: 22px;
	border: 2px solid black;
    max-width: 10rem;
    min-width: 8rem;
    font-size: 0.75rem;
	text-align: center;
	text-transform: uppercase;
    font-weight: 500;
    margin: 0.5rem;
}
.navbar-light .navbar-nav .nav-item:hover, .navbar-light .navbar-nav .nav-item:active{
    border-color: #5960A3
}

.navbar-light .navbar-nav .nav-item .nav-link{
    color: black;
    padding: 0.25rem;
}
.navbar-light .navbar-nav .nav-item .nav-link:hover{
    color:rgba(0, 0, 0, 0.5);
}


@media only screen and (max-width: 768px){
    .navbar{
        background-color: white !important;
    }

    .navbar-nav{
        -webkit-align-items: center;
                align-items: center;
    }
}
