html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit; 
    vertical-align: baseline;
    font-family: 'Rubik', sans-serif;
}

html{
	height: 100%;
}

.Timer{
	display: flex;
	flex-direction: row;
	color: black;
}

.Timer p{
	margin: auto;
}
.Timer-adjust{
	display: flex;
	flex-direction: row;
	background-color: white;
	color: black;
	border-radius: 8px;
	vertical-align: middle;
	padding: 0rem 1rem 0rem 1rem;
}

.Timer-adjust p{
	margin: auto;
	margin-right: 0.5rem;
	font-size: 1rem;
}

.Timer-adjust .adjust{
	display: flex;
	flex-direction: column;
}


.Timer-adjust .adjust .icon-top, .Timer-adjust .adjust .icon-bottom{
	padding: 0px;
	margin: 0px;
	color: #B8B8B7;
}

.Timer-adjust .adjust .icon-top .MuiSvgIcon-root-1, .Timer-adjust .adjust .icon-bottom .MuiSvgIcon-root-1{
	height: 0.75em;
	width: 0.75em;
}

.Timer-adjust .adjust .icon-top .MuiIconButton-root-1, .Timer-adjust .adjust .icon-bottom .MuiIconButton-root-1{
	transition: none;

}